
import React from "react";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faHome, faSearch } from "@fortawesome/free-solid-svg-icons";
import { Col, Row, Form, Breadcrumb, InputGroup } from '@themesberg/react-bootstrap';

import { TransactionsTable } from "../../components/Tables";
import { useEffect, useState } from "react";

export default (props) => {
  console.log('Price: ', props);
  const { top10 } = props;
  const [dataTable, setDataTable] = useState([]);
  let tableData = []
  useEffect(() => {
    top10.map((coin, index) => {
      tableData.push({
        "invoiceNumber": index,
        "status": coin.RAW.BRL.VOLUME24HOUR,
        "subscription": coin.CoinInfo.FullName,
        "price": coin.RAW.BRL.LOW24HOUR,
        "issueDate": coin.RAW.BRL.PRICE,
        "dueDate": coin.RAW.BRL.HIGH24HOUR
      })
    })
    setDataTable(tableData);
  }, [tableData])
  return (
    <>
    <div className="d-flex justify-content-between flex-wrap flex-md-nowrap align-items-center py-4">
      <div className="d-block mb-4 mb-md-0">
        <Breadcrumb className="d-none d-md-inline-block" listProps={{ className: "breadcrumb-dark breadcrumb-transparent" }}>
          <Breadcrumb.Item><FontAwesomeIcon icon={faHome} /></Breadcrumb.Item>
          <Breadcrumb.Item>Dashboard</Breadcrumb.Item>
          <Breadcrumb.Item active>Cotação</Breadcrumb.Item>
        </Breadcrumb>
        <h4>Cotação</h4>
        <p className="mb-0">Seu web analytics dashboard de cotações.</p>
      </div>
    </div>

    <div className="table-settings mb-4">
      <Row className="justify-content-between align-items-center">
        <Col xs={8} md={6} lg={3} xl={4}>
          <InputGroup>
            <InputGroup.Text>
              <FontAwesomeIcon icon={faSearch} />
            </InputGroup.Text>
            <Form.Control type="text" placeholder="Buscar" />
          </InputGroup>
        </Col>
        <Col xs={4} md={2} xl={1} className="ps-md-0 text-end">
        </Col>
      </Row>
    </div>
    <TransactionsTable dataTable={dataTable}/>
  </>
  );
};
